<template>
  <van-nav-bar
  :title="childMsg"
  :leftArrow ="!leftArrow"
  class="mr-nav-bar"
  @click-left="onClickLeft"
>
<div class="slot-right" slot="right">
  <span slot='right' v-if="share"  @click="onShare">转发</span>
  <span slot="right1" v-if="cart" @click="onCart">购物车</span>
  <span slot="right2" v-if="contact" @click="onContact">联系</span>
</div>
</van-nav-bar>
</template>
<script>
export default {
  props:[
    'leftArrow',
    'childMsg',
    'share',
    'cart',
    'contact'
  ],
  methods:{
    onClickLeft(){
      this.$router.back();
    },
    onShare(){
       this.$toast('我是转发');
    },
    onCart(){
     this.$toast('我是购物车');
    },
    onContact(){
     this.$toast('我是商务联系');
    }
  }
}
</script>

<style lang="scss">
.van-nav-bar .van-icon{
  color:#333;
}
</style>


